import Quagga from "quagga";
export default {
  name: "pInputForm",
  data() {
    return {
      model: {
        log: ""
      }
    };
  },
  setup() {},
  methods: {
    console(msg) {
      this.model.log = msg;
    },
    doStop() {
      Quagga.stop();
    },
    doStart() {
      Quagga.init({
        inputStream: {
          name: "Live",
          type: "LiveStream",
          target: document.querySelector("#interactive.viewport")
        },
        function(err) {
          if (err) {
            console(err);
            return;
          }
          console("Initialization finished. Ready to start");
          Quagga.start();
        }
      });
      Quagga.onProcessed(this.doProcessed);
      Quagga.onDetected(this.doDetected);
    },
    doProcessed() {},
    doDetected() {}
  }
};